body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(231,3,173,1) 0%, rgba(255,240,1,1) 100.2% );

}    
.Tilt {
    background-image: linear-gradient( 69.5deg,  rgba(189,73,255,0.99) 18.6%, rgba(254,76,227,1) 85.9% );
}
.form{
    width: 700px;
    background: repeating-radial-gradient(
        circle,
        purple,
        purple 10px,
        #4b026f 10px, 
        #4b026f 20px
        );
    }
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 ;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.App {
    text-align: center;
  }
  
  .center {
    display: flex;
    justify-content: center;
  }
  
  .particles {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
  }
